"use client";
import React from "react";
import { cn } from "@/lib/utils";

export function Subheading({
  as: Tag = "h3",
  dark,
  className,
  children,
}: {
  as?: React.ElementType;
  dark?: boolean;
  className?: string;
  children: React.ReactNode;
}) {
  return (
    <Tag
      className={cn(
        "text-lg font-semibold tracking-wide",
        dark ? "text-white" : "text-gray-900",
        className
      )}
    >
      {children}
    </Tag>
  );
}
